export default {
	data() {
		return {
			active: false,
			value: "",
			activeIndex: 0,
			currentpage: '全部',
			add_animation: false,
			list2: [],
			list: []
		}
	},
	onLoad() {

	},
	mounted() {
		if (uni.getStorageSync('list') == '') {
			uni.setStorageSync('list', JSON.stringify([]))
			this.list = JSON.parse(uni.getStorageSync('list'))
			console.log(this.list)
		} else {
			
			this.list = JSON.parse(uni.getStorageSync('list'))
		}
	},
	computed: {
		listData() {
			let newList = []
			switch (this.activeIndex) {
				case 0:
					return this.list
					break;
				case 1:
					this.list.forEach(item => {
						if (!item.checked) {
							newList.push(item)
						}
					})
					return newList
					break;
				case 2:
					this.list.forEach(item => {
						if (item.checked) {
							newList.push(item)
						}
					})
					return newList
					break;
			}
		}
	},
	methods: {
		openCreate() {
			this.active === true ? this.closeAnimation() : this.openAnimation();
		},
		openAnimation() {
			this.active = !this.active,
				setTimeout(() => {
					this.add_animation = !this.add_animation
				}, 50)
		},
		closeAnimation() {
			this.add_animation = !this.add_animation
			setTimeout(() => {
				this.active = !this.active
			}, 350)
		},
		createToDo() {
			if (this.value !== "") {
				let newTodo = {
					id: this.list.length,
					content: this.value,
					checked: false
				}
				this.list.unshift(newTodo)
				uni.setStorageSync('list', JSON.stringify(this.list))
			} else {
				uni.showToast({
					title: '请输入内容',
					icon: 'none',
					duration: 2000
				})
			}
			this.value = ""
			this.closeAnimation()
		},
		finish(id) {
			let index = this.list.findIndex(item => item.id == id)
			let finishItem = this.list[index]
			this.list[index].checked = !this.list[index].checked
			if (finishItem.checked == true) {
				this.list.splice(index, 1)
				this.list.push(finishItem)
				uni.setStorageSync('list', JSON.stringify(this.list))
			}else{
				this.list.splice(index, 1)
				this.list.unshift(finishItem)
				uni.setStorageSync('list', JSON.stringify(this.list))
			}
		},
		tab(index) {
			this.activeIndex = index
			switch (index) {
				case 0:
					this.currentpage = '全部'
					break;
				case 1:
					this.currentpage = '待办'
					break;
				case 2:
					this.currentpage = '已完成'
					break;
			}
		},
		deleteTodo(id) {
			let _this = this
			uni.showModal({
				title: '确定要删除吗',
				success: function(res) {
					if (res.confirm) {
						let deleteItem = _this.list.findIndex(item => item.id == id)
						_this.list.splice(deleteItem, 1)
						uni.setStorageSync('list', JSON.stringify(_this.list))
					} else {
						return
					}
				}
			})

		}
	}
}
